<template>
    <born-embryology />
    <born-embryo-transfer />
</template>

<script>
import { onMounted, onUnmounted, provide, watch } from "vue";
import { useBornFertilizationInit } from "@/composable/born";
import { emitter } from "@/event-bus";

import BornEmbryology from "@/components/born/embryology.vue";
import BornEmbryoTransfer from "@/components/born/embryo-tranfer.vue";

export default {
    components: {
        BornEmbryology,
        BornEmbryoTransfer
    },
    setup() {
        const { embryology, form, fillForm, save } = useBornFertilizationInit();

        watch(embryology, fillForm);

        onMounted(() => {
            if (embryology.value) {
                fillForm(embryology.value);
            }
            emitter.on("embryo-transfer:save", save);
        });

        onUnmounted(() => {
            emitter.off("embryo-transfer:save", save);
        });

        provide("form", form);
    }
};
</script>
